.title {
  flex: 1;
  display: flex;
}
.title > div:first-child {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  margin-right: 15px;
}
.title > div:last-child {
  flex: 0 0 60px;
  font-size: 14px;
  text-align: center;
}
.title > div:last-child > i {
  font-size: 20px;
}
.des {
  display: flex;
  padding-bottom: 15px;
  position: relative;
}
.des::before,
.des::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.des::before {
  display: none;
  top: 0;
}
.des::after {
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .des::before,
  .des::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .des::before,
  .des::after {
    transform: scaleY(0.33);
  }
}
.des > ul {
  flex: 0 0 120px;
  max-width: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
  position: relative;
}
.des > ul::before,
.des > ul::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  border-left: 1px solid #DDD;
  height: 100%;
  top: 0;
  bottom: 0;
}
.des > ul::before {
  display: none;
  left: 0;
}
.des > ul::after {
  right: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .des > ul::before,
  .des > ul::after {
    transform: scaleX(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .des > ul::before,
  .des > ul::after {
    transform: scaleX(0.33);
  }
}
.des > ul > li {
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.des > ul > li:last-child {
  color: #AEAEAE;
}
.des > div {
  flex: 1;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  color: #AEAEAE;
}
.comment_list_header .user_avatar {
  width: 40px;
  height: 40px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.comment_list_header .user_avatar > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.comment_list_header :global(.am-card-header-extra) {
  flex: initial;
}
.comment_list_header :global(.am-card-header-extra) > span {
  padding: 6px 12px;
  background-color: #F7F7F7;
}
.comment_reply_list {
  margin-top: 15px;
  padding-top: 10px;
  position: relative;
}
.comment_reply_list::before,
.comment_reply_list::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.comment_reply_list::before {
  top: 0;
}
.comment_reply_list::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .comment_reply_list::before,
  .comment_reply_list::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .comment_reply_list::before,
  .comment_reply_list::after {
    transform: scaleY(0.33);
  }
}
.comment_reply_list span {
  color: #34A5EA;
}
.comment {
  display: flex;
  align-items: center;
  padding-right: 15px;
}
.comment_input {
  flex: 1;
}
.comment > ul {
  flex: 1;
  display: flex;
  color: #AEAEAE;
}
.comment > ul > li {
  flex: 1;
}
.comment > ul > li > i {
  margin-right: 2px;
}
.comment > ul > li:not(:last-child) {
  margin-right: 10px;
}
